<script setup>
    import {
        Head
    } from '@vueuse/head'
</script>
<template>

    <Head>
        <title>{{ title }}</title>
        <meta name="description" :content="description">

        <!-- Social -->
        <meta property="og:title" :content="title">
        <meta property="og:description" :content="description">
        <!-- <meta property="og:image" content="https://picsum.photos/1200/675"> -->

        <!-- Twitter -->
        <meta name="twitter:title" :content="title">
        <meta name="twitter:description" :content="description">
        <!-- <meta name="twitter:image" content="https://picsum.photos/1200/675"> -->
        <!-- <meta name="twitter:card" content="summary_large_image"> -->
    </Head>
    <section class="py-10">
        <div class="container mt-5 mt-lg-10 text-white">
            <BreadcrumbsVue>
                <template v-slot:title>
                    Gallery
                </template>
                <template v-slot:title-link>
                    Gallery
                </template>
            </BreadcrumbsVue>
            <div class="row g-0 mt-5">
                <div class="col-sm-6 col-lg-4" v-for="( gallery , i) in gallery" :key="i">
                    <div class="card card-hover-gradient inverted equal-1-1 media media-image" :data-bp='` { "imgSrc": "${gallery.thumbnail}"  }`'>
                        <div class="card-wrap">
                            <div class="card-footer text-shadow mt-auto">
                                <h5 class="mb-0">{{ gallery.title }}</h5>
                            </div>
                        </div>
                        <span class="background" :style="`background-image: url('${gallery.thumbnail}')`">
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </section>

</template>

<script>
    import BreadcrumbsVue from "../components/Breadcrumbs.vue"
    //   import galleryCardVue from "../components/galleryCard.vue"
    import gallery from '../../data/gallery.json';
    export default {
        components: {
            BreadcrumbsVue,
            //  galleryCardVue
        },
        data: () => ({
            gallery,
            title: "Gallery | Jethro Jorolan",
            description: "My Galleries"
        }),
    }
</script>